.Main {
  background-color: #ccc;
  background: -webkit-gradient(linear, left bottom, left top, from(#aaa), to(#ddd));
  background: -webkit-linear-gradient(bottom, #aaa, #ddd);
  background: -o-linear-gradient(bottom, #aaa, #ddd);
  background: linear-gradient(0deg, #aaa, #ddd);
  width: 100%;
}




